import React, { PureComponent } from 'react'
import ReactMarkdown from 'react-markdown'
import rehypeRaw from 'rehype-raw'
import Download from '../images/download.svg'
import {Seo} from "../../components/common";

const tagColors = ['#DC3023', 'rgb(240, 157, 56)','rgb(0, 216, 208)']
const hostPath = 'https://raw.github.com/biyunwu/academic-web/master/src/data/papers/'

export default class Papers extends PureComponent {

    state = {
        isBrowserReady : false,
        viewportWidth: 0
    }

    updateState = () => {
        this.setState(
            { isBrowserReady: typeof window !== 'undefined',
                viewportWidth: typeof window !== 'undefined' ? window.innerWidth : 0
            }
        )
    }

    componentDidMount() {
        window.scrollTo(0,0)
        this.updateState();
        window.addEventListener('resize', this.updateState);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateState);
    }

    generatePaperInfo = (isEn, title, titleCN, tags, pathname, idx) => {
        return (
            <li key={idx}>
                {/*<a className='paper-title' href={hostPath + pathname}>*/}
                {/*<span style='font-family: STKaiti, Kaiti, Helvetica, sans-serif; font-size: 20px;'></span>*/}
                    <ReactMarkdown rehypePlugins={[rehypeRaw]} >
                        { isEn ? title : (title + "<br>" + titleCN )}
                    </ReactMarkdown>
                {/*</a>*/}
                <div className='indent'>
                    <div className='tag-container'>
                        {this.getTags(tags)}
                    </div>
                    <a href={hostPath + pathname} target='_blank' rel='noopener noreferrer'>
                        <img className='download-icon' src={Download} alt='Click to download'/>
                    </a>
                </div>
            </li>
        )
    }

    getTags = (tags, classname) =>
        tags.length > 1
        ? tags.map((num, idx) => <span className='tag' style={{background: tagColors[num]}} key={classname || idx}/>)
        : <span className='tag' style={{background: tagColors[tags]}}/>

    getTagLabel = (category, idx) => 
        <div className='tag-label-container' key={category}>
            <div id={'color'+idx} className='label' key={'color'+idx}
                style={{backgroundColor: tagColors[idx]}}
            >
                {category}
            </div>
        </div>

    render () {
        const isEn = this.props.lang === "en"
        const {seoTitle, seoTitleCN, categories, categoriesCN, shortCategories, shortCategoriesCN, items} = this.props.papers
        const {isBrowserReady, viewportWidth} = this.state
        const widthLimiter = 780
        const tagLabels = viewportWidth < 700
            ? shortCategories.map((category, idx) => this.getTagLabel(category, idx))
            : categories.map((category, idx) => this.getTagLabel(category, idx))
        const tagLabelsCN = viewportWidth < 700
            ? shortCategoriesCN.map((category, idx) => this.getTagLabel(category, idx))
            : categoriesCN.map((category, idx) => this.getTagLabel(category, idx))
        const papers = items.map((item, idx) => this.generatePaperInfo(isEn, item.title, item.titleCN, item.tags, item.pathname, idx))
        const titles = items.map(item => isEn? item.title : item.titleCN)

        return (
            <div id='maincontent-container'>
                <main id='papers-div'>
                    <Seo title={isEn ? seoTitle : seoTitleCN}
                         description={isEn ? titles.join(". ") : titles.join("；")}
                         location={isEn ? "/papers" : "/cn/papers"}
                    />
                    {
                        isBrowserReady && viewportWidth >= widthLimiter &&
                        <div className='options'>
                            <h2>{isEn ? "Categories" : "类别"}</h2>
                            <div className='sticky'>
                                {isEn ? tagLabels : tagLabelsCN}
                            </div>
                        </div>
                    }
                    <div className='papers'>
                        <h2>{isEn ? "A Few Papers" : "部分论文"}</h2>
                        {
                            isBrowserReady && viewportWidth < widthLimiter &&
                            <div className='options'>
                                <div className='sticky'>
                                    {isEn ? tagLabels : tagLabelsCN}
                                </div>
                            </div>
                        }
                        <ul>
                            {papers}
                        </ul>
                    </div>
                </main>
            </div>
        )
    }
}